import React from 'react';

import { useState } from "react";
import api from "./services/api";
import { FiSearch } from "react-icons/fi";
import { AiOutlineReload } from "react-icons/ai";
import './styles.css';
import video from './img/video.mp4'
import Footer from './Footer/footer';
import Anuncio1 from './services/anuncio1';

function App() {

const[input , setInput]=useState('');
const[inputLog, setInputLog]=useState('');
const[inputUf,setInputUf]=useState('');
const[inputCity,setInputCity]=useState('');
const [cep, setCep]=useState({});
const[log, setLog]=useState({});


function darkmode(){
  document.documentElement.classList.toggle(`darkmode`)
}

async function search(){
 
 

    try {
      const response = await api.get(`${input}/json/`);
      setCep(response.data);
      setInput('');
      console.log(response);
    }
    catch {
      alert("Erro ao buscar Cep");
      setInput('');
    }

  }
  

  async function searchRua(){
    
    try{
      fetch (`https://viacep.com.br/ws/${inputUf}/${inputCity}/${inputLog}/json/`)
      .then(response=>response.json()) 
      .then((data)=> {
        const list=document.querySelector('#lista')

       data.map((item)=>{
          const li = document.createElement('li');
          const span = document.createElement('span');
          const comp = document.createElement('p');
          li.setAttribute('id',item.id);
          span.setAttribute('id',item.id);
          comp.setAttribute('id',item.id);
          li.innerHTML=`Cep - ${item.cep}`;
          span.innerHTML=`Bairro - ${item.bairro}`;
          comp.innerHTML=`Mesmo Cep - ${item.complemento}`;
          list.appendChild(li);
          list.appendChild(span);
          list.appendChild(comp);
        })
      })
    }
    catch {
      alert("Erro ao buscar Cep");
      setInput('');
    }

    
  }
  
    

  const enterKey=(e) =>{
  if(e.key === 'Enter'){
    search();
  }
};

const enterKeyRua=(e) =>{
  if(e.key === 'Enter'){
    searchRua();
  }
};

const reload=()=>{
  window.location.reload()
}

  return (
    <>
    <div className='head'>

      <video className='videoTag' autoPlay loop muted>
          <source src={video} type='video/mp4'  />
      </video>
      <div className='modnoturne'>
          <h4>Modo Noturno</h4>
          <div onClick={darkmode}>
          <svg xmlns="http://www.w3.org/2000/svg" width="35" height="35" fill="currentColor" class="bi bi-brightness-high-fill" viewBox="0 0 16 16">
          <path d="M12 8a4 4 0 1 1-8 0 4 4 0 0 1 8 0zM8 0a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-1 0v-2A.5.5 0 0 1 8 0zm0 13a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-1 0v-2A.5.5 0 0 1 8 13zm8-5a.5.5 0 0 1-.5.5h-2a.5.5 0 0 1 0-1h2a.5.5 0 0 1 .5.5zM3 8a.5.5 0 0 1-.5.5h-2a.5.5 0 0 1 0-1h2A.5.5 0 0 1 3 8zm10.657-5.657a.5.5 0 0 1 0 .707l-1.414 1.415a.5.5 0 1 1-.707-.708l1.414-1.414a.5.5 0 0 1 .707 0zm-9.193 9.193a.5.5 0 0 1 0 .707L3.05 13.657a.5.5 0 0 1-.707-.707l1.414-1.414a.5.5 0 0 1 .707 0zm9.193 2.121a.5.5 0 0 1-.707 0l-1.414-1.414a.5.5 0 0 1 .707-.707l1.414 1.414a.5.5 0 0 1 0 .707zM4.464 4.465a.5.5 0 0 1-.707 0L2.343 3.05a.5.5 0 1 1 .707-.707l1.414 1.414a.5.5 0 0 1 0 .708z"/>
          </svg></div>
        </div>
        
        <h1 className="title">Busque pelo Cep</h1>
        
    </div>

    <div className="container">
    <h4>Busque pelo Cep ou pelo nome da Rua</h4>
        <div className="containerInput">
          <input
          type="text" placeholder="Digite o Cep..." 
          value={input}
          onChange={(e)=> setInput(e.target.value)}
          onKeyDown={enterKey}
          />
          
          <div className="buttonSeacrch"  onClick={search}
          >
          <FiSearch size={25} color="black"/>
          </div>
      </div>

      
      {Object.keys(cep).length>0 && (
        <main className="main">
        <h2>{cep.cep}</h2>
        <span>{cep.logradouro}</span>
        <span>{cep.bairro}</span>
        <span>{cep.localidade} - {cep.uf}</span>
        <span>DDD da Area : {cep.ddd}</span>
        <span>{cep.complemento}</span>
      </main>
      )}
      

      <h4> No sabe seu CEP? Busca pelo seu endereo:</h4>

      <div className="containerInputUf">
          <input
          type="text" placeholder="Digite seu Estado" 
          value={inputUf}
          onChange={(e)=> setInputUf(e.target.value)}
          onKeyDown={enterKey}
          />
          
          
      </div>
        
      <div className="containerInputCidade">
          <input
          type="text" placeholder="Digite sua Cidade" 
          value={inputCity}
          onChange={(e)=> setInputCity(e.target.value)}
          onKeyDown={enterKey}
          />
          
      </div>
      <div className="containerInputLog">
          <input
          type="text" placeholder="Digite seu Logradouro " 
          value={inputLog}
          onChange={(e)=> setInputLog(e.target.value)}
          onKeyDown={enterKeyRua}
          />
          <div className="buttonSeacrch"  onClick={searchRua}
          >
          <FiSearch size={25} color="black" right='5px'/>
          </div>
          
      </div>
      <div className='buttonReset' onClick={reload}>
          Clique e Faa uma Nova Pesquisa<AiOutlineReload  size={25} color="black"/>
          </div>
      
      <ul id='lista'>
      </ul>

      
      <Anuncio1/>
      <Footer/>
    </div>
    </>
  );
}

export default App;
